import { Injectable } from "@angular/core";
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import 'rxjs/add/operator/map';
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { LineData } from '../data.interface';
import {AuthService} from "../services/auth.service";
import {User} from "../model/model.user";
import {Router} from "@angular/router";
import {AppComponent} from "../app.component";


@Injectable()
export class LineDataService {
    //let response;
    //currentUser:string;

    constructor(private http: Http, public authService: AuthService, public router: Router) {
      //  this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
    }

    getLinedata(this) {
        let headers = new Headers();
        headers.append('Accept', 'application/json');
        var base64Credential: string = btoa(sessionStorage.getItem('currentUser')+ ':' + sessionStorage.getItem('currentUserPass'));
        headers.append("Authorization", "Basic " + base64Credential);

        let options = new RequestOptions();
        options.headers = headers;

        return this.http.get(AppComponent.API_URL+"/customer/getline", options)
       // return this.http.get('http://yblpvdakdwha01.yesbank.com:8600/customer/getline', options)
            .map(res => res.json());

        //  private dataSubject = new BehaviorSubject<lineData[]>(this.lineData);
        //  $data = this.dataSubject.asObservable();

    }

}



