import { Component, ElementRef, ViewChild, AfterViewInit, Input } from "@angular/core";
import 'rxjs/add/operator/map';
import { DoughnutDataService }  from '../../services/doughnutDataService.service';
import * as d3 from 'd3';


interface doughnutData {
event: number;
id: number;
label: string;
percentage: number;
value: number;
img: string;
}

interface Datum {
event: number;
id: number;
label: string;
percentage: number;
value: number;
img: string;
}

@Component({
    selector: "doughnut-chart",
    templateUrl: "./doughnut.component.html",
    styleUrls: ['./doughnut.component.css']
})

export class DoughnutChartComponent implements AfterViewInit {



    @ViewChild("donutChart") element: ElementRef;

    private host: d3.Selection < any, any, any, any > ;
    private svg: d3.Selection < any, any, any, any > ;
    private width: number;
    private height: number;
    private radius: number;
    private htmlElement: HTMLElement;
    private doughnutData: doughnutData[];

    constructor(private container: ElementRef, private doughnutDataService: DoughnutDataService) {}

    ngAfterViewInit() {
            this.htmlElement = this.element.nativeElement;
            this.host = d3.select(this.htmlElement);

            this.doughnutDataService.getDoughnutdata().subscribe(data => {
            console.log(data);
            this.doughnutData = data;
            this.renderChart();
        });

  /*this.doughnutDataService.$data.subscribe(data => {
             this.doughnutData = data;
             this.renderChart();
         });
*/
    }

    public selectedId: any;
    public baseUrl: string =
        'https://raw.githubusercontent.com/amanjain325/angular-2-d3-charts/master/src/';



    public renderChart() {
        let chartComponent = this;
        let imageWidth = 40;
        let imageHeight = 40;
        let width = 400;
        let height = 400;
        let radius = 250;

    let  pie = d3.pie().sort(null);

        let arc = d3.arc()
            .outerRadius(150)
            .innerRadius(70);

        let arcNew = d3.arc()
            .outerRadius(160)
            .innerRadius(70);

        let svg = d3.select(this.container.nativeElement).append('svg')
            .attr('width', 330)
            .attr('height', 330)
            .append('g')
            .attr('transform', 'translate(' + (width - radius + 10) + ',' + (height - radius + 10) + ')');

        let values = this.doughnutData.map(data => data.percentage);
        let Label = this.doughnutData.map(data => data.value);
        let doughnutColor = ["#98abc5", "#8a89a6", "#7b6888", "#6b486b", "#a05d56", "#d0743c", "#ff8c00"];



        let g = svg.selectAll('.arc')
            .data(pie(values))
            .enter().append('g')
            .attr('class', 'arc');

        g.append('path')
            .attr('d', <any> arc)
            .style('stroke', 'white')
            .attr("fill", function(d, i) {return doughnutColor[i];})
            .attr('id', function(d, i) {
                return chartComponent.doughnutData[i].event;});


        g.append('g')
            /*.attr('transform', function(d) {
                return 'translate(' + arc.centroid(d) + ')';
            })*/
            .attr("transform", (datum: any) => {
                datum.innerRadius = 0;
                datum.outerRadius = this.radius;
                return "translate(" + arc.centroid(datum) + ")";
            })
            .append('svg:image')
            .attr('xlink:href', function(d, i) {
                return chartComponent.baseUrl + chartComponent.doughnutData[i].img;
            })
            .attr('id', function(d, i) {
                return chartComponent.baseUrl + chartComponent.doughnutData[i].img;
            })
            .attr('width', imageWidth)
            .attr('height', imageHeight)
            .attr('x', -1 * imageWidth / 2)
            .attr('y', -1 * imageHeight / 2)
            .attr('cursor', 'pointer');


        svg.append('svg:image')
            .attr('id', 'center_image')
            .attr('x', -60)
            .attr('y', -60)
            .attr('width', 120)
            .attr('height', 120)
            .attr('cursor', 'pointer').attr('xlink:href', chartComponent.baseUrl + 'assets/img/6.png')
            .on('click', function click(d) {
                // alert('Middle circle clicked');
            });



    }
}



