import { Component, Input, OnInit , ElementRef, ViewChild, AfterViewInit } from "@angular/core";
import 'rxjs/add/operator/map';
import { LineDataService }  from '../../services/LineDataService.service';
import { LineData } from '../../data.interface';
import * as d3 from 'd3';

@Component({
    selector: 'line-chart',
    templateUrl: './line-chart.component.html'
})

export class LineChartCompoent implements AfterViewInit {
    @Input() data: LineData[];
    private width: number;
    private height: number;
    private margin = {
        top: 5,
        right: 5,
        bottom: 60,
        left: 45
    };
    private x: any;
    private y: any;
    private svg: any;
    private g: any;
    lineData: any;
    private valueline: any;


    constructor(private container: ElementRef, private LineDataService: LineDataService) {}

    ngAfterViewInit() {
        this.LineDataService.getLinedata().subscribe(data => {
            //console.log(data);
            this.data = data;
            this.initSvg();
            this.initAxis();
            this.drawAxis();
            this.drawLine();
        });
    }


    private initSvg() {
        this.svg = d3.select(this.container.nativeElement)
            .select("svg");

        this.width = +this.svg.attr("width") - this.margin.left - this.margin.right;
        this.height = +this.svg.attr("height") - this.margin.top - this.margin.bottom;

        this.g = this.svg.append("g")
            .attr("transform", "translate(" + this.margin.left + "," + this.margin.top + ")");
    }

    private initAxis() {
        this.x = d3.scaleBand()
            .rangeRound([0, this.width])
            .padding(5);
        this.y = d3.scaleLinear()
            .rangeRound([this.height, 0]);
        this.x.domain(this.data.map((d) => d.date));
        this.y.domain([0, d3.max(this.data, (d) => d.total)]);
    }

    private drawAxis() {
        this.g.append("g")
            .attr("class", "axis axis--x")
            .attr("transform", "translate(0," + this.height + ")")
            .call(d3.axisBottom(this.x))
            .selectAll("text")
            .style("text-anchor", "end")
            .attr("dx", "-.8em")
            .attr("dy", ".15em")
            .attr("transform", "rotate(-65)");

        this.g.append("g")
            .attr("class", "axis axis--y")
            //.call(d3.axisLeft(this.y).ticks(10, "%"))
            .call(d3.axisLeft(this.y).ticks(6, "k"))
            .append("text")
            .attr("class", "axis-title")
            .attr("transform", "rotate(-90)")
            .attr("y", 6)
            .attr("dy", "0.71em")
            .attr("text-anchor", "end")
            .call(d3.axisBottom(this.y));
    }

    private drawLine() {
        //console.log(this.data);
        this.valueline = d3.line()

            .x((d) => {
                return this.x(d['date']);
            })
            .y((d) => {
                return this.y(d['total'])
            });

        this.g.append('path')
            .datum(this.data)
            .attr("class", "path")
            .attr("fill", "none")
            .attr("stroke", "#a05d56")
            .attr("stroke-width", 1.5)
            .attr("d", this.valueline(this.data));

    }
}








