import { Component, ElementRef, NgZone, Input, OnDestroy, OnInit ,ViewEncapsulation,ViewChild, AfterViewInit} from '@angular/core';
import 'rxjs/add/operator/map';
import {D3Service,D3,Axis,BrushBehavior,BrushSelection,D3BrushEvent,ScaleLinear,ScaleOrdinal,Selection,Transition} from 'd3-ng2-service';
import { BarDataService }  from '../../services/barDataService.service';

interface BarData {
name: string;
yVal: number;
}


@Component({
    selector: 'app-d3graph',
    templateUrl: './d3graph.component.html',
    styleUrls: ['./d3graph.component.css'],
    encapsulation: ViewEncapsulation.None
})

export class D3graphComponent implements AfterViewInit {


  @Input() BarData: BarData[];

    private d3: D3;
    private parentNativeElement: any;
    private d3Svg: Selection < SVGSVGElement, any, null, undefined > ;

    constructor(private element: ElementRef, private ngZone: NgZone, private d3Service: D3Service, private barDataService: BarDataService) {
       this.d3 = d3Service.getD3();
       this.parentNativeElement = element.nativeElement;

    }


    ngAfterViewInit () {
        let self = this;
        let d3 = this.d3;
        let d3ParentElement: any;
        let svg: any;
        let name: string;
        let yVal: number;
        let colors: any = [];
/*let data: {
name: string,
yVal: number
}[] = [];*/
        let padding: number = 30;
        let width: number = 400;
        let height: number = 400;
        let xScale: any;
        let yScale: any;
        let xColor: any;
        let xAxis: any;
        let yAxis: any;
        let BarData: BarData[];







        if (this.parentNativeElement !== null) {

            svg = d3.select(this.parentNativeElement)
                .append('svg') // create an <svg> element
                .attr('width', width) // set its dimensions
                .attr('height', height);

            colors = ["#98abc5", "#8a89a6", "#7b6888", "#6b486b", "#a05d56", "#d0743c", "#ff8c00"];

this.barDataService.getBardata().subscribe(data => {
            //console.log(data);
            this.BarData = data;

            xScale = d3.scaleBand()
                            .domain(this.BarData.map(function(d) {
                                return d.name;
                            }))
                            .range([0, 350]);

                        yScale = d3.scaleLinear()
                            .domain([0, d3.max(this.BarData, function(d) {
                                return d.yVal
                            })])
                            .range([341, 0]);

                        xAxis = d3.axisBottom(xScale) // d3.js v.4
                            .ticks(5)
                            .scale(xScale);

                        yAxis = d3.axisLeft(xScale) // d3.js v.4
                            .scale(yScale)
                            .ticks(7);

                        svg.append("g")
                            .attr("class", "axis")
                            .attr("transform", "translate(" + (padding) + "," + padding + ")")
                            .call(yAxis);

                        svg.append('g') // create a <g> element
                            .attr('class', 'axis') // specify classes
                            .attr("transform", "translate(" + padding + "," + (height - padding) + ")")
                            .call(xAxis); // let the axis do its thing

                        var rects = svg.selectAll('rect')
                            .data(this.BarData);
                        rects.size();

                        var newRects = rects.enter();

                        newRects.append('rect')
                            .attr('x', function(d, i) {
                                return xScale(d.name);
                            })
                            .attr('y', function(d) {
                                return yScale(d.yVal);
                            })
                            .attr("transform", "translate(" + (padding - 5 + 35) + "," + (padding - 5) + ")")
                            .attr('height', function(d) {
                                return height - yScale(d.yVal) - (2 * padding) + 5
                            })
                            .attr('width', 30)
                            .attr('fill', function(d, i) {
                                return colors[i];
                            });
        });

         /*  BarData = [{"name" : "Savings", "yVal" : 100},{"name" : "Current", "yVal" : 400},{"name" : "Fixed Deposit", "yVal" : 200},{"name" : "Recurring", "yVal" : 300},{"name" : "Abc", "yVal" : 500}];*/


        }

    }
}



