import { Routes, RouterModule } from '@angular/router';
import {ProfileComponent} from "./components/profile/profile.component";
import {LoginComponent} from "./components/login/login.component";
import {RegisterComponent} from "./components/register/register.component";
//import {UrlPermission} from "./urlPermission/url.permission";
//import { AuthGuard } from './services/routeguard';
//import { AuthErrorHandler } from './services/auth-error-handler';
//import { AuthRequestOptions } from './services/auth-request';
import { DashboardSearchComponent } from './components/dashboard-search/dashboard-search.component';



const appRoutes: Routes = [{
        path: 'dashboard',
        component: ProfileComponent
        //canActivate: [AuthGuard]
    },
    {
        path: 'login',
        component: LoginComponent
    },
    {
        path: 'register',
        component: RegisterComponent
    },
    {
    path: 'dashboardsearch',
    component: DashboardSearchComponent
    },

    // otherwise redirect to profile
    {
        path: '**',
        redirectTo: '/login'
    }
];

export const routing = RouterModule.forRoot(appRoutes);


DashboardSearchComponent



